.App {
  text-align: center;
}

* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  color: rgb(253, 246, 246);
}

.container {
  background-color: rgb(78, 105, 122);
  width: 1000px;
  height: 550px;
  margin: 20px auto;
}
.calculator-body {
  background-image: url(pattern.jpg);
  background-position: center;
  width: 320px;
  height: 520px;
  margin: 0 auto;
  top: 20px;
  position: relative;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.result {
  background-color: #919caad3;
  background-size: contain;
  background-size: 10px 10px 10px 10px;
  margin: 0px 10px 0px 10px;
  height: 120px;
}

.result {
  text-align: right;
  height: 90px;
  margin: 10px 20px;
  font-size: 30px;
}
.button {
  height: 400px;
}
.operator,
.number {
  width: 50px;
  height: 50px;
  margin: 15px;
  float: left;
  border-radius: 50%;
  border-width: 0;
  font-weight: bold;
  font-size: 15px;
}
.number {
  background-color: #102635c4;
}

.number,
.operator {
  cursor: pointer;
}
.operator:active,
.number:active {
  font-size: 13px;
}
.operator:focus,
.number:focus {
  outline: 0;
}
.operator {
  font-size: 20px;
  background-color: #360221d7;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
